.contact{
    max-width: 600px;
    padding-bottom: 2rem;
    padding-top: 3rem;
    padding-right: 4rem;
    padding-left: 6rem;
    text-align: center;
    display: block;
    list-style: none;
    margin: auto;
}
.FormContato{
    width: 600px;
    margin: auto;
    color: gray;
    padding-bottom: 15px;
}
.FormContato h2{
    background-color: rgba(212, 207, 207, 0.644);
    height: 80px;
    font-size: 20px;
    text-align: center;
    padding-top: 27px;
    letter-spacing: 2px;
    font-family:fantasy;
}






.contact h2{
    color: black;
    font-size: 1.5rem;
    font-weight: 400;
    margin: 0;
}

.contact p{
    border-bottom: 1px dotted #ddd;
    color: #777;
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    line-height: 0 0 1rem;
}